body {
  background: linear-gradient(-45deg, #51f5b6, #cabbec, #3dabd3, #5be7c7);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.App {
  height: 100vh;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.blur {
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(8px);
  height: 100vh;
  width: 50%;
}

.great-header {
  margin-top: 30px;
  margin-bottom: 20px;
  font-size: 24px;
  text-align: center;
  color: rgb(95, 95, 95);
}

.buttons {
  display: flex;
  justify-content: space-between;
  padding: 20px 80px;
}

.image {
  width: 300px;
  height: 200px;
}

.contactImage {
  width: 250px;
  height: 250px;
  border-radius: 10px;
}

.question {
  font-size: 20px;
  font-weight: bold;
  color: #3f51b5;
}

.completeTextContainer {
  margin: 20px auto;
}

.completeText {
  display: inline-block;
  padding: 15px 0;
  border-radius: 15px;
  width: 300px;
  background-color: #98a7fd;
  box-shadow: 4px 4px 32px 15px rgba(34, 60, 80, 0.2);
  color: white;
  font-style: 20px;
}

.wrapInput {
  display: flex;
  justify-content: center;
  margin-top: 40px;
}

.card {
  position: relative;
  display: inline-block;
}

.checkIcon {
  position: absolute;
  top: -10px;
  left: -10px;
  background-color: #fff;
  border-radius: 50%;
}

@media (max-width: 980px) {
  .image {
    width: 200px;
    height: 150px;
  }

  .contactImage {
    width: 150px;
    height: 150px;
  }
}

@media (max-width: 600px) {
  .great-header {
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 20px;
    text-align: center;
    color: rgb(95, 95, 95);
  }
}
